:root {
  --bs-heading-color: #424242 !important;
  --bs-link-color-rgb: #424242 !important;
}

body[data-bs-theme=light] a {
  color: #424242;
}
body[data-bs-theme=light] a:hover {
  color: black;
}
body[data-bs-theme=dark] a {
  color: rgba(173, 181, 189, 0.7490196078);
}
body[data-bs-theme=dark] a:hover {
  color: white;
}